"use client";

import { useState } from "react";

export default function AIDetector() {
  const [imagePreview, setImagePreview] = useState<string | null>(null);
  const [result, setResult] = useState<string>("");

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (file) {
      // This creates a temporary local URL for the selected file
      setImagePreview(URL.createObjectURL(file));
      console.log("File selected:", file.name);
    }
  };

  const checkAI = async () => {
    if (!imagePreview) return alert("Upload an image first!");
    setResult("Checking...");

    try {
      const response = await fetch("/api/check-ai", { method: "POST" });
      const data = await response.json();
      setResult(data.message);
    } catch (error) {
      setResult("Error checking image");
    }
  };

  const buttonStyle: React.CSSProperties = {
    border: "2px solid #0CC405",
    backgroundColor: "black",
    color: "white",
    borderRadius: "10px",
    boxShadow: "0 0 15px rgba(12, 196, 5, 0.4)",
    cursor: "pointer",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    fontSize: "16px",
    fontWeight: "600",
    transition: "all 0.2s ease",
  };

  return (
    <main className="relative min-h-screen w-full overflow-hidden bg-black">
      <div className="graph-bg" />

      <input 
        type="file" 
        id="fileInput" 
        style={{ display: "none" }} 
        onChange={handleFileChange} 
        accept="image/*" 
      />

      {/* Upload Button */}
      <button 
        onClick={() => document.getElementById("fileInput")?.click()}
        style={{
          ...buttonStyle,
          position: "absolute",
          top: "350px",
          left: "620px",
          width: "288px",
          height: "70px",
        }}
      >
        <img 
          src="/img/image_logo.png" 
          style={{ width: '24px', height: '24px', marginRight: '12px' }} 
          alt="logo" 
        />
        <span>Upload Image</span>
      </button>

      {/* --- NEW SECTION: Image Display --- */}
      {imagePreview && (
        <div style={{
          position: "absolute",
          top: "0px", // Positioned between the two buttons
          left: "480px",
          width: "570px",
          height: "680px",
          border: "1px dashed #555",
          borderRadius: "10px",
          overflow: "hidden",
          display: "flex",
          alignItems: "center",
          justifyContent: "center"
        }}>
          <img 
            src={imagePreview} 
            alt="Preview" 
            style={{ width: "100%", height: "100%", objectFit: "contain" }} 
          />
        </div>
      )}
      {/* ---------------------------------- */}

      {/* Action Button */}
      <button 
        onClick={checkAI}
        style={{
          ...buttonStyle,
          position: "absolute",
          top: "650px",
          left: "620px",
          width: "288px",
          height: "70px",
        }}
      >
        {result || "Check for AI"}
      </button>
    </main>
  );
}